'use client';

import React, { useState, useEffect, useMemo, useCallback, useRef } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import {usePathname} from 'next/navigation';
import {useAuth} from '@/lib/supabase/auth-provider';
import {useLanguageStore} from '@/stores/languageStore';
import { useGlobalLoading } from '@/contexts/GlobalLoadingContext';
import {DefaultAvatar, ProfileImageContainer,} from '@/components/ui/ProfileImageContainer';
import PortalMenuItem from './PortalMenuItem';
import MobileNavigationMenu from './MobileNavigationMenu';
import MobilePortalMenu from './MobilePortalMenu';
import {PORTAL_MENU} from '@/config/navigation';
import {Menu as MenuIcon, ChevronRight} from 'lucide-react';
import LanguageSelector from './LanguageSelector';

const Header: React.FC = () => {
  const { isAuthenticated, userProfile, user, signOut, isLoading, isInitialized } = useAuth();
  const { currentLanguage } = useLanguageStore();
  const { setIsLoading: setGlobalLoading } = useGlobalLoading();
  const pathname = usePathname();
  
  // Ïä§ÌÅ¨Î°§ ÏÉÅÌÉú Í¥ÄÎ¶¨
  const [isScrolled, setIsScrolled] = useState(false);
  const [canScrollRight, setCanScrollRight] = useState(false);
  const [showScrollHint, setShowScrollHint] = useState(false);
  const menuContainerRef = useRef<HTMLDivElement>(null);

  // Ïä§ÌÅ¨Î°§ ÏÉÅÌÉú ÌôïÏù∏ Ìï®Ïàò
  const checkScrollState = useCallback(() => {
    if (menuContainerRef.current) {
      const { scrollLeft, scrollWidth, clientWidth } = menuContainerRef.current;
      setIsScrolled(scrollLeft > 10);
      setCanScrollRight(scrollLeft < scrollWidth - clientWidth - 10);
    }
  }, []);

  // Ïä§ÌÅ¨Î°§ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨
  const handleScroll = useCallback(() => {
    checkScrollState();
  }, [checkScrollState]);

  // Ïª¥Ìè¨ÎÑåÌä∏ ÎßàÏö¥Ìä∏ Ïãú Ïä§ÌÅ¨Î°§ ÏÉÅÌÉú Ï≤¥ÌÅ¨ Î∞è ÌûåÌä∏ ÌëúÏãú
  useEffect(() => {
    const timer = setTimeout(() => {
      checkScrollState();
      if (canScrollRight) {
        setShowScrollHint(true);
        // 3Ï¥à ÌõÑ ÌûåÌä∏ Ïà®ÍπÄ
        setTimeout(() => setShowScrollHint(false), 3000);
      }
    }, 100);

    return () => clearTimeout(timer);
  }, [checkScrollState, canScrollRight]);

  // ÏúàÎèÑÏö∞ Î¶¨ÏÇ¨Ïù¥Ï¶à Ïãú Ïä§ÌÅ¨Î°§ ÏÉÅÌÉú Ïû¨ÌôïÏù∏
  useEffect(() => {
    const handleResize = () => {
      setTimeout(checkScrollState, 100);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [checkScrollState]);

  // üêõ ÎîîÎ≤ÑÍ∑∏ Î™®Îìú Ï≤¥ÌÅ¨
  const isDebugMode = process.env.NODE_ENV === 'development';

  // ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ÌÅ¥Î¶≠ Ìï∏Îì§Îü¨
  const handleLinkClick = useCallback((href: string) => {
    if (pathname !== href) {
      setGlobalLoading(true);
    }
  }, [pathname, setGlobalLoading]);

  // üéØ DB ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄÎßå ÏÇ¨Ïö© (OAuth ÌÜ†ÌÅ∞ Ïù¥ÎØ∏ÏßÄ Ï†úÏô∏)
  const getUserInfo = useCallback(() => {
    // 1. DB ÌîÑÎ°úÌïÑÏù¥ ÏûàÏúºÎ©¥ DB ÏÇ¨Ïö© (ÏÇ¨Ïö©ÏûêÍ∞Ä Í¥ÄÎ¶¨ÌïòÎäî ÌîÑÎ°úÌïÑ)
    if (userProfile) {
      return {
        nickname: userProfile.nickname || userProfile.email?.split('@')[0] || user?.email?.split('@')[0] || 'ÏÇ¨Ïö©Ïûê',
        email: userProfile.email || user?.email || 'Ïù¥Î©îÏùº Ï†ïÎ≥¥ ÏóÜÏùå', 
        avatar_url: userProfile.avatar_url || null, // DBÏùò ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄÎßå ÏÇ¨Ïö©
        provider: 'profile',
        source: 'userProfile'
      };
    }
    
    // 2. DB ÌîÑÎ°úÌïÑÏù¥ ÏóÜÏùÑ ÎïåÎäî JWT ÌÜ†ÌÅ∞Ïùò Í∏∞Î≥∏ Ï†ïÎ≥¥Îßå ÏÇ¨Ïö© (Ïù¥ÎØ∏ÏßÄ Ï†úÏô∏)
    if (user) {
      return {
        nickname: user.user_metadata?.name || user.user_metadata?.full_name || user.email?.split('@')[0] || 'ÏÇ¨Ïö©Ïûê',
        email: user.email || 'Ïù¥Î©îÏùº Ï†ïÎ≥¥ ÏóÜÏùå',
        avatar_url: null, // JWT ÌÜ†ÌÅ∞Ïùò Ïù¥ÎØ∏ÏßÄÎäî ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏùå
        provider: user.app_metadata?.provider || 'unknown',
        source: 'token'
      };
    }
    
    // 3. Í∏∞Î≥∏Í∞í
    return {
      nickname: 'ÏÇ¨Ïö©Ïûê',
      email: 'Î°úÍ∑∏Ïù∏ ÌõÑ Ïù¥Î©îÏùºÏù¥ ÌëúÏãúÎê©ÎãàÎã§',
      avatar_url: null,
      provider: 'none',
      source: 'default'
    };
  }, [userProfile, user]); // userProfile Ïö∞ÏÑ†

  // üîç ÏïàÏ†ïÏ†ÅÏù∏ Ïù∏Ï¶ù ÏÉÅÌÉú ÌôïÏù∏ Ìï®Ïàò
  const getStableAuthState = useCallback(() => {
    // Ï¥àÍ∏∞ÌôîÍ∞Ä ÏôÑÎ£åÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞
    if (!isInitialized) {
      return {
        showUserArea: false,
        showHamburger: false,
        showLoading: true,
        reason: 'not_initialized'
      };
    }

    // Î°úÎî© Ï§ëÏù∏ Í≤ΩÏö∞
    if (isLoading) {
      return {
        showUserArea: false,
        showHamburger: false,
        showLoading: true,
        reason: 'loading'
      };
    }

    // Ïù∏Ï¶ùÎêòÏßÄ ÏïäÏùÄ Í≤ΩÏö∞
    if (!isAuthenticated || !user) {
      return {
        showUserArea: false,
        showHamburger: true,
        showLoading: false,
        reason: 'not_authenticated'
      };
    }

    // Ïù∏Ï¶ùÎêú Í≤ΩÏö∞ (JWTÎßå ÏûàÍ±∞ÎÇò ÌîÑÎ°úÌïÑÎèÑ ÏûàÎäî Í≤ΩÏö∞ Î™®Îëê ÏÇ¨Ïö©Ïûê ÏòÅÏó≠ ÌëúÏãú)
    return {
      showUserArea: true,
      showHamburger: false,
      showLoading: false,
      reason: 'authenticated'
    };
  }, [isAuthenticated, user, isLoading, isInitialized]);

  // üîç ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ Î°úÎî© ÏÉÅÌÉú ÌôïÏù∏
  const isProfileImageLoading = useCallback(() => {
    // DB ÌîÑÎ°úÌïÑÏù¥ Î°úÎî© Ï§ëÏù∏ÏßÄÎßå ÌôïÏù∏ (JWT Ïù¥ÎØ∏ÏßÄÎäî ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏùå)
    return isAuthenticated && user && userProfile === null;
  }, [isAuthenticated, user, userProfile]);

  // ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
  const userInfo = getUserInfo();
  const profileImageLoading = isProfileImageLoading();
  const stableAuthState = getStableAuthState();

  // ÎîîÎ≤ÑÍπÖ Î°úÍ∑∏ (Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå)
  useEffect(() => {
    if (process.env.NODE_ENV === 'development') {
      console.log('üîç [Header] Ïù∏Ï¶ù ÏÉÅÌÉú ÎîîÎ≤ÑÍπÖ:', {
        'üìä Í∏∞Î≥∏ ÏÉÅÌÉú': {
          isAuthenticated,
          isLoading,
          isInitialized,
          hasUser: !!user,
          hasUserProfile: !!userProfile
        },
        'üéØ Í≥ÑÏÇ∞Îêú ÏÉÅÌÉú': {
          stableAuthState,
          profileImageLoading,
          userInfo: {
            source: userInfo.source,
            hasAvatar: !!userInfo.avatar_url,
            provider: userInfo.provider
          }
        }
      });

      // üö® Í¥ÄÎ¶¨Ïûê Í∂åÌïú ÏÉÅÌÉú Î≥ÑÎèÑ Î°úÍπÖ
      if (isAuthenticated && !isLoading) {
        const isAdminFromProfile = userProfile?.is_admin;
        const isSuperAdminFromProfile = userProfile?.is_super_admin;
        const isAdminFromMetadata = user?.user_metadata?.is_admin;
        const isSuperAdminFromMetadata = user?.user_metadata?.is_super_admin;
        
        const finalIsAdmin = isAdminFromProfile || isSuperAdminFromProfile || isAdminFromMetadata || isSuperAdminFromMetadata;
        
        // üêõ Í∞úÎ∞ú ÌôòÍ≤Ω ÏûÑÏãú Í¥ÄÎ¶¨Ïûê Ï≤¥ÌÅ¨
        const isDevTempAdmin = process.env.NODE_ENV === 'development' && 
                              isAuthenticated && 
                              !isLoading && 
                              !userProfile &&
                              user;

        // üîß Í∞úÎ∞ú ÌôòÍ≤Ω fallback Í¥ÄÎ¶¨Ïûê Ï≤¥ÌÅ¨
        const isDevFallbackAdmin = process.env.NODE_ENV === 'development' && 
                                  isAuthenticated && 
                                  !isLoading &&
                                  user &&
                                  (Date.now() - (window as any).authStartTime || 0) > 2000;

        console.log('üö® [Header] Í¥ÄÎ¶¨Ïûê Í∂åÌïú ÏÉÅÌÉú:', {
          'üîë Í∂åÌïú Ï†ïÎ≥¥': {
            profile_isAdmin: isAdminFromProfile,
            profile_isSuperAdmin: isSuperAdminFromProfile,
            metadata_isAdmin: isAdminFromMetadata,
            metadata_isSuperAdmin: isSuperAdminFromMetadata,
            finalIsAdmin,
            devTempAdmin: isDevTempAdmin,
            devFallbackAdmin: isDevFallbackAdmin
          },
          'üéØ Í≤∞Í≥º': {
            showAdminMenus: finalIsAdmin || isDevTempAdmin || isDevFallbackAdmin ? '‚úÖ' : '‚ùå'
          }
        });
      }
    }
  }, [isAuthenticated, isLoading, userProfile, user, stableAuthState, profileImageLoading]);

  return (
    <header className='border-b border-gray-200 bg-white relative'>
      <div className='container mx-auto px-2 sm:px-4 py-2'>
        {/* Î©îÏù∏ Ìó§Îçî ÎùºÏù∏ */}
        <div className='flex items-center justify-between w-full gap-2 sm:gap-4'>
          {/* Ï¢åÏ∏°: Î°úÍ≥† + Î©îÎâ¥ */}
          <div className='flex items-center gap-2 sm:gap-4 flex-1 min-w-0'>
            {/* Î°úÍ≥† */}
            <div className='flex items-center flex-shrink-0'>
              <Link 
                href="/"
                prefetch={true}
                onClick={() => handleLinkClick('/')}
              >
                <Image
                  src='/images/logo.png'
                  alt='logo'
                  width={40}
                  height={40}
                  priority
                  className='w-8 h-8 sm:w-10 sm:h-10'
                />
              </Link>
            </div>

            {/* Î™®Î∞îÏùº Ìè¨ÌÑ∏Î©îÎâ¥ - Î™®Î∞îÏùºÍ≥º ÌÉúÎ∏îÎ¶øÏóêÏÑú ÌëúÏãú */}
            <div className='md:hidden flex items-center'>
              <MobilePortalMenu />
            </div>

            {/* Îç∞Ïä§ÌÅ¨ÌÉë Î©îÎâ¥ - Îç∞Ïä§ÌÅ¨ÌÉëÏóêÏÑúÎßå ÌëúÏãú */}
            <div className='hidden md:flex flex-1 relative'>
              {/* Î©îÎâ¥ Ïª®ÌÖåÏù¥ÎÑà */}
              <div 
                ref={menuContainerRef}
                className='overflow-x-auto scrollbar-hide scroll-smooth'
                onScroll={handleScroll}
                style={{
                  scrollbarWidth: 'none',
                  msOverflowStyle: 'none',
                }}
              >
                <div className='flex items-center space-x-1 sm:space-x-2 min-w-max'>
                  {PORTAL_MENU.map((menuItem) => {
                    // üîê Í∂åÌïúÎ≥Ñ Î©îÎâ¥ ÎÖ∏Ï∂ú Ï°∞Í±¥ (ÏöîÍµ¨ÏÇ¨Ìï≠Ïóê Îî∞Îùº ÏàòÏ†ï)
                    
                    // VOTE Î©îÎâ¥Îäî Ìï≠ÏÉÅ ÎÖ∏Ï∂ú
                    if (menuItem.type === 'vote') {
                      return (
                        <PortalMenuItem
                          key={menuItem.path}
                          portalType={menuItem.type}
                        />
                      );
                    }

                    // COMMUNITY, PIC, NOVEL Î©îÎâ¥Îäî Î°úÍ∑∏Ïù∏Ìïú Í¥ÄÎ¶¨ÏûêÎßå ÎÖ∏Ï∂ú
                    const isAdminOnlyMenu = ['community', 'pic', 'novel'].includes(menuItem.type);
                    if (isAdminOnlyMenu) {
                      // Î°úÍ∑∏Ïù∏ÌïòÏßÄ ÏïäÏïòÏúºÎ©¥ Ïà®ÍπÄ
                      if (!isAuthenticated) {
                        return null;
                      }

                      // üîÑ userProfile Î°úÎî© ÏÉÅÌÉú Ï≤¥ÌÅ¨
                      const isUserProfileLoading = isAuthenticated && !userProfile && !isLoading;

                      // userProfileÏù¥ ÏïÑÏßÅ Î°úÎî© Ï§ëÏù¥Î©¥ Î©îÎâ¥Î•º Ïà®ÍπÄ (Î°úÎî© ÌõÑ Ï†êÏßÑÏ†Å ÌëúÏãú)
                      if (isUserProfileLoading) {
                        if (process.env.NODE_ENV === 'development') {
                          console.log(`‚è≥ [Header] ${menuItem.type} Î©îÎâ¥ - userProfile Î°úÎî© Ï§ë...`);
                        }
                        return null;
                      }

                      // üîç Í¥ÄÎ¶¨Ïûê Í∂åÌïú ÌôïÏù∏ (DBÏùò userProfile Ïö∞ÏÑ†, Î∞±ÏóÖÏúºÎ°ú user metadata ÌôúÏö©)
                      const isAdmin = userProfile?.is_admin || 
                                     userProfile?.is_super_admin || 
                                     user?.user_metadata?.is_admin || 
                                     user?.user_metadata?.is_super_admin;

                      // üêõ Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑú userProfileÏù¥ ÏóÜÎäî Í≤ΩÏö∞ ÏûÑÏãú Í¥ÄÎ¶¨Ïûê Í∂åÌïú Î∂ÄÏó¨
                      const isDevTempAdmin = process.env.NODE_ENV === 'development' && 
                                            isAuthenticated && 
                                            !isLoading && 
                                            !userProfile &&
                                            user; // JWT ÏÇ¨Ïö©ÏûêÍ∞Ä ÏûàÏúºÎ©¥ ÏûÑÏãú Í¥ÄÎ¶¨ÏûêÎ°ú Í∞ÑÏ£º

                      // üîß Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑú userProfile Î°úÎî©Ïù¥ Ïã§Ìå®Ìïú Í≤ΩÏö∞ÎèÑ Í≥†Î†§
                      const isDevFallbackAdmin = process.env.NODE_ENV === 'development' && 
                                                isAuthenticated && 
                                                !isLoading &&
                                                user &&
                                                // userProfileÏù¥ 2Ï¥à Ïù¥ÏÉÅ Î°úÎìúÎêòÏßÄ ÏïäÏúºÎ©¥ Í∞úÎ∞úÌôòÍ≤ΩÏóêÏÑúÎäî Í¥ÄÎ¶¨ÏûêÎ°ú Í∞ÑÏ£º
                                                (Date.now() - (window as any).authStartTime || 0) > 2000;

                      // ÎîîÎ≤ÑÍπÖÏö© Î°úÍ∑∏ (Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå)
                      if (process.env.NODE_ENV === 'development') {
                        console.log(`üîç [Header] ${menuItem.type} Î©îÎâ¥:`, {
                          isAdmin,
                          isDevTempAdmin,
                          isDevFallbackAdmin,
                          shouldShow: isAdmin || isDevTempAdmin || isDevFallbackAdmin,
                          isLoading: isUserProfileLoading
                        });
                      }

                      // Í¥ÄÎ¶¨ÏûêÍ∞Ä ÏïÑÎãàÎ©¥ Ïà®ÍπÄ (Í∞úÎ∞úÌôòÍ≤Ω ÏûÑÏãúÍ¥ÄÎ¶¨ÏûêÎäî Ï†úÏô∏)
                      if (!isAdmin && !isDevTempAdmin && !isDevFallbackAdmin) {
                        return null;
                      }
                    }

                    return (
                      <PortalMenuItem
                        key={menuItem.path}
                        portalType={menuItem.type}
                      />
                    );
                  })}
                </div>
              </div>

              {/* Ïò§Î•∏Ï™Ω Í∑∏ÎùºÎîîÏñ∏Ìä∏ ÌéòÏù¥Îìú Ìö®Í≥º (Îçî ÎßéÏùÄ ÏΩòÌÖêÏ∏†Í∞Ä ÏûàÏùÑ Îïå) */}
              {canScrollRight && (
                <div className='absolute right-0 top-0 bottom-0 w-8 bg-gradient-to-l from-white via-white/80 to-transparent pointer-events-none flex items-center justify-end pr-1'>
                  <ChevronRight 
                    className={`w-3 h-3 text-gray-400 transition-all duration-300 ${
                      showScrollHint ? 'animate-pulse' : ''
                    }`} 
                  />
                </div>
              )}

              {/* ÏôºÏ™Ω Í∑∏ÎùºÎîîÏñ∏Ìä∏ ÌéòÏù¥Îìú Ìö®Í≥º (Ïä§ÌÅ¨Î°§Îêú ÏÉÅÌÉúÏùº Îïå) */}
              {isScrolled && (
                <div className='absolute left-0 top-0 bottom-0 w-6 bg-gradient-to-r from-white via-white/80 to-transparent pointer-events-none' />
              )}
            </div>
          </div>

          {/* Ïö∞Ï∏° Î©îÎâ¥ - Ìï≠ÏÉÅ ÌëúÏãú */}
          <div className='flex items-center gap-3'>
            {/* Ïñ∏Ïñ¥ ÏÑ†ÌÉùÍ∏∞ - Î™®Îì† ÌôîÎ©¥ÏóêÏÑú ÌëúÏãú */}
            <div className='flex items-center justify-center h-8 sm:h-10'>
              <LanguageSelector />
            </div>

            {/* Î™®Î∞îÏùº ÌñÑÎ≤ÑÍ±∞ Î©îÎâ¥ - Î™®Î∞îÏùºÍ≥º ÌÉúÎ∏îÎ¶øÏóêÏÑú ÌëúÏãú */}
            <div className='md:hidden flex items-center justify-center h-8 sm:h-10'>
              <MobileNavigationMenu />
            </div>

            {/* ÌîÑÎ°úÌïÑ/Î°úÍ∑∏Ïù∏ Î≤ÑÌäº - Îç∞Ïä§ÌÅ¨ÌÉëÏóêÏÑúÎßå ÌëúÏãú (Î™®Î∞îÏùº/ÌÉúÎ∏îÎ¶øÏùÄ MobileNavigationMenuÍ∞Ä Ï≤òÎ¶¨) */}
            <div className='hidden md:flex items-center justify-center h-8 sm:h-10'>
              {stableAuthState.showLoading ? (
                // Î°úÎî© Ï§ëÏùº Îïå shimmer Ìö®Í≥º
                <div className="w-8 h-8 sm:w-10 sm:h-10 rounded-lg shimmer-effect bg-gray-200 flex-shrink-0"></div>
              ) : stableAuthState.showUserArea ? (
                // Ïù∏Ï¶ùÎêú ÏÇ¨Ïö©Ïûê ÏòÅÏó≠
                <Link 
                  href='/mypage' 
                  className='block w-8 h-8 sm:w-10 sm:h-10 flex-shrink-0 rounded-lg overflow-hidden'
                  prefetch={true}
                  onClick={() => handleLinkClick('/mypage')}
                >
                  {profileImageLoading || (isAuthenticated && !userInfo.avatar_url && userProfile === null) ? (
                    // DB ÌîÑÎ°úÌïÑ Î°úÎî© Ï§ëÏù¥Í±∞ÎÇò ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏóÜÎäî Í≤ΩÏö∞ shimmer Ìö®Í≥ºÎßå ÌëúÏãú
                    <div className="w-full h-full rounded-lg shimmer-effect bg-gray-200"></div>
                  ) : userInfo.avatar_url ? (
                    <ProfileImageContainer
                      avatarUrl={userInfo.avatar_url}
                      width={40} // Îç∞Ïä§ÌÅ¨ÌÉë Í∏∞Ï§Ä 40px
                      height={40}
                      borderRadius={8}
                      className="w-full h-full object-cover"
                    />
                  ) : (
                    <DefaultAvatar 
                      width={40} // Îç∞Ïä§ÌÅ¨ÌÉë Í∏∞Ï§Ä 40px
                      height={40} 
                      className="w-full h-full"
                    />
                  )}
                </Link>
              ) : stableAuthState.showHamburger ? (
                // ÎØ∏Ïù∏Ï¶ù ÏÇ¨Ïö©Ïûê ÌñÑÎ≤ÑÍ±∞ Î©îÎâ¥
                <Link 
                  href='/mypage' 
                  className='flex items-center justify-center w-8 h-8 sm:w-10 sm:h-10 flex-shrink-0'
                  prefetch={true}
                  onClick={() => handleLinkClick('/mypage')}
                >
                  <div className='w-full h-full hover:bg-gray-100 rounded-lg transition-colors cursor-pointer border border-gray-200 flex items-center justify-center'>
                    <MenuIcon className="w-4 h-4 sm:w-5 sm:h-5 text-gray-700" />
                  </div>
                </Link>
              ) : null}
            </div>
          </div>
        </div>

        {/* Î™®Î∞îÏùº Ìè¨ÌÑ∏ Î©îÎâ¥ ÎùºÏù∏ Ï†úÍ±∞ - ÏÉÅÎã®ÏúºÎ°ú Ïù¥ÎèôÎê® */}
      </div>

      <style jsx>{`
        .scrollbar-hide {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
        .scrollbar-hide::-webkit-scrollbar {
          display: none;
        }
        .shimmer-effect {
          background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
          background-size: 200% 100%;
          animation: shimmer 1.5s ease-in-out infinite;
        }
        @keyframes shimmer {
          0% { background-position: -200% 0; }
          100% { background-position: 200% 0; }
        }
      `}</style>
    </header>
  );
};

export default Header;
